* {
  box-sizing: border-box;
  padding: 0;
}

html,
body {
  font-family: system-ui;
  color: #ff9800;
  -webkit-text-stroke-color: black;
  -webkit-text-stroke-width: 0.9px;
  background-image: radial-gradient(
    circle,
    #a5c4f2,
    #8e9dbf,
    #73788e,
    #565661,
    #373737
  );
  background-attachment: fixed;
}

#root {
  display: flex;
}

.container {
  margin: auto;
  padding: 0 20px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.se-button {
  font-size: 50px;
  background-image: linear-gradient(
    to right,
    #fdfbde,
    #f7e8ab,
    #f6d278,
    #f9b846,
    #ff9b00
  );
  font-style: oblique;
  color: chocolate;
  margin-top: 5px;
  margin-bottom: 5px;
}

.gu-button {
  width: 150px;
  height: 40px;
  font-size: 20px;
  background: repeating-linear-gradient(45deg, #b1b1b1, transparent 150px);
  -webkit-text-stroke-width: 0.3px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 6px 0px;
}

.game {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 40px;
  padding: 10px 50px;
  background-image: linear-gradient(
    to right top,
    #a52a2a,
    #7c202a,
    #541923,
    #2d1218,
    #000000
  );
}

.board {
  width: 735px;
  padding: 5px;
}

.square {
  font-size: 30px;
  font-weight: bold;
  height: 90px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 90px;
}

.square:active {
  transition-duration: 1s;
}

.start {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
}

.start img {
  width: 600px;
}

header {
  height: 65px;
}

header img {
  width: 90px;
}

.top-area {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 6px 0px;
}

.top-area #spinner {
  width: 30px;
  height: 30px;
}

.name-input {
  height: 40px;
  color: orange;
  text-align: center;
  border-style: inset;
  font-size: 24px;
  margin-top: 10px;
}

.start-footer {
  display: grid;
  margin-top: 10px;
  background-image: linear-gradient(
    to left bottom,
    #f8fbff,
    #ccd0d4,
    #a2a6ab,
    #797e83,
    #53585e
  );
  border-style: double;
  padding-left: 10px;
  padding-right: 10px;
}

.start-footer h2 {
  margin: 0;
  text-align: center;
  margin-bottom: 5px;
  font-size: 34px;
}

h1,
h2,
h3 {
  background-image: linear-gradient(
    to right,
    #fdfbde,
    #f7e8ab,
    #f6d278,
    #f9b846,
    #ff9b00
  );
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  padding: 0;
  margin: 0;
}

.clock {
  width: 40px;
  height: 40px;
  margin: 0px 10px;
}

.timer-area {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 991.98px) {
  .start h2 {
    font-size: 24px;
  }

  .start img {
    width: 500px;
  }

  header img {
    width: 70px;
  }

  .game {
    margin-top: 0px;
    padding: 8px 35px;
  }

  .game h2,
  h3 {
    font-size: 1.1em;
  }

  .board {
    width: 600px;
  }

  .square {
    width: 70px;
    height: 70px;
  }

  .square img {
    width: 50px;
    height: 55px;
  }

  .gu-button {
    width: 100px;
    font-size: 14px;
    height: 20px;
  }

  .se-button {
    font-size: 40px;
  }

  .name-input {
    font-size: 20px;
    margin-top: 3px;
  }

  .clock {
    width: 35px;
    height: 35px;
    margin: 0px 8px;
  }
}

@media only screen and (max-width: 575.98px) {
  .start h2 {
    font-size: 20px;
  }

  .start img {
    width: 400px;
  }

  header img {
    width: 50px;
  }

  .game {
    margin-top: 0px;
    padding: 5px 20px;
  }

  .game h2,
  h3 {
    font-size: 0.8em;
  }

  .board {
    width: 350px;
  }

  .square {
    width: 42px;
    height: 42px;
  }

  .square img {
    width: 22px;
    height: 27px;
  }

  .gu-button {
    width: 80px;
    font-size: 12px;
    height: 20px;
  }

  .se-button {
    font-size: 30px;
  }

  .name-input {
    font-size: 14px;
    margin-top: 3px;
  }

  .clock {
    width: 25px;
    height: 25px;
    margin: 0px 5px;
  }

  .top-area #spinner {
    width: 20px;
    height: 20px;
  }

  .top-area h3 {
    font-size: 0.6em;
  }
}
